@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;500;600;800&display=swap');

body {
    font-family: 'Montserrat', sans-serif;
    margin: 0px;
    background-color: #000;
    color: #FFF;
    font-weight: 500;
}

img {
    border-radius: 20px;
}

.texto-destaque {
    color: #7bebcf;
}

a {
    text-decoration: none;
    color: #4059D7;
}

h1 {
    font-size: 4rem;
}

.textGradient {
    display: inline-block;
    background: rgb(123, 235, 207);
    background: linear-gradient(60deg, rgba(123, 235, 207, 1) 0%, rgba(30, 120, 255, 1) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin: 0px;
}

.borderButtonGradient {
    display: block;
    background: rgb(123, 235, 207);
    background: linear-gradient(60deg, rgba(123, 235, 207, 1) 0%, rgba(30, 120, 255, 1) 100%);
    border-radius: 10px;
    padding: 2px;
    width: 100%;
    margin: 20px auto;
    transition: all .2s ease-in-out;
}

.borderButtonGradient:hover {
    transform: translate(0px, -3px);
    display: block;
    transition: all .2s ease-in-out;
}

.borderButtonGradient div {
    background-color: #000;
    color: #FFF;
    border-radius: 10px;
    padding-block: 6px;
    border: none;
    cursor: pointer;
}

.borderButtonGradient div p {
    margin: 0px;
    padding-left: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

#Steam .borderButtonGradient div p::before {
    content: '';
    background-image: url(/playfolio/img/icon-steam.svg);
    background-repeat: no-repeat;
    background-size: contain;
    filter: invert(1);
    padding: 10px;
    margin-right: 10px;
}

#Epic .borderButtonGradient div p::before {
    content: '';
    background-image: url(/playfolio/img/icon-epic.svg);
    background-repeat: no-repeat;
    background-size: contain;
    filter: invert(1);
    padding: 10px;
    margin-right: 10px;
}

main {
    width: 1200px;
    max-width: 100%;
    margin: auto;
    align-items: center;
}

.MainContent {
    padding-top: 20px;
}

.MainContent>article:first-of-type {
    padding: 20px;
    max-width: 70%;
}

.cards-list {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.cards-list>div {
    width: 270px;
    padding: 11px;
    background-color: #323232;
    border-radius: 20px;
}

.cards-list div img {
    width: 100%;
}

.description {
    height: 150px;
    overflow: hidden;
}

.timePlayed {
    font-size: 12px;
    text-align: center;
}

footer {
    bottom: 0px;
    margin-top: 20px;
    text-align: center;
    width: 100%;
}

footer a {
    font-weight: 600;
}

footer a.politica-de-privacidade {
    font-weight: 400;
    color: #FFF;
}

footer small {
    display: block;
    margin: auto;
    padding: 10px 0px;
    padding-bottom: 20px;
    width: 1200px;
    max-width: calc(100% - 10px);
    background-color: #323232;
    border-radius: 20px 20px 0px 0px;
}

@media only screen and (max-width: 1000px) {
    main .App {
        display: flex;
        gap: 0px;
        justify-content: space-between;
        align-items: center;
    }
}

@media only screen and (max-width: 1220px) {
    .cards-list>div {
        width: 30%;
    }
}

@media only screen and (max-width: 880px) {
    .cards-list>div {
        width: 45%;
    }
}

@media only screen and (max-width: 690px) {
    main .App {
        display: block;
    }

    footer {
        position: unset;
    }

    .MainContent>article:first-of-type {
        max-width: 100%;
    }
}

@media only screen and (max-width: 612px) {
    .cards-list>div {
        width: calc(100% - 60px);
    }

    .description {
        height: auto;
        overflow: hidden;
    }
}